@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737373}.container{max-width:100%;margin:0 20px}

.header{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality/static/media/header.3573d221.jpg);background-size:cover;padding:80px 0 178px;overflow-x:hidden}.header-partners{display:flex;align-items:center}.header-partners-item{margin-left:60px}.header-partners-item:first-child{margin-left:0}.header-nav{margin-top:45px}.header-nav-navs{text-decoration:none;font-weight:normal;font-size:19px;line-height:22px;color:#FFFFFF;margin-left:74px}.header-nav-navs:first-child{margin-left:0}.header-title{width:664px;font-weight:normal;font-size:57px;line-height:67px;color:#fff;margin:108px 0 132px 0}.header-button{display:inline-block;padding:35px 151px;text-decoration:none;font-weight:normal;font-size:32px;line-height:37px;color:#fff;background:#b78a3f}

.pres{box-sizing:border-box;position:relative;padding:100px 60px 50px}.pres-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#fff;margin-bottom:30px}.pres-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#fff}.pres-slide img{padding-left:15%}.pres>.swiper-button-prev,.pres .swiper-button-next{color:#b78a3f}.pres>.swiper-button-prev{left:10px}.pres>.swiper-button-next{right:10px}.pres>.swiper-button-disabled{opacity:0}.pres-pagination{display:flex;justify-content:center;margin-top:66px}.pres-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #878a3f;opacity:1;transition:0.3s background-color}.pres-pagination>.swiper-pagination-bullet-active{background-color:#b78a3f}

.pollution{padding:50px 0}.pollution-title{text-align:center;font-weight:normal;font-size:35px;line-height:41px;color:#fff}.pollution-map{height:560px;margin:42px 0}.pollution-info{font-weight:normal;font-size:23px;line-height:27px;color:#fff}

.optimization{padding:50px 0}.optimization-name{font-weight:normal;font-size:35px;line-height:41px;color:#fff;text-align:center}.optimization-subname{font-weight:normal;font-size:23px;line-height:27px;color:#fff;margin:42px 0 30px}.optimization-list{display:flex;flex-wrap:wrap;justify-content:space-between}.optimization-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}@media (max-width: 980px){.optimization-list-item{width:calc((100% / 2) - 70px)}}.optimization-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.optimization-list-item-tooltip{position:absolute;left:100%;width:440px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-color:#dadada;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);clip-path:polygon(100% 0, 100% 100%, 75px 100%, 0 50%, 75px 0);z-index:1;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.optimization-list-item-tooltip-switched{left:auto;left:initial;right:100%;-webkit-clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);clip-path:polygon(calc(100% - 75px) 0, 100% 50%, calc(100% - 75px) 100%, 0 100%, 0 0);padding:16px 75px 16px 16px}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.footer{border-top:1px solid #fff;margin-bottom:50px}.footer-partners{display:flex;align-items:center}.footer-partners-item{margin-right:120px}.footer-partners-item:last-child{margin-right:0}.footer-text-wrap{display:flex;justify-content:space-between}.footer-nav{display:flex;flex-direction:column;margin-bottom:88px}.footer-nav-navs{text-decoration:none;font-weight:normal;font-size:17px;line-height:20px;color:#FFFFFF;margin-bottom:20px}.footer-nav-navs:first-child{margin-left:0}.footer-nav-text{max-width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#fff}.footer-text{font-style:normal;font-weight:300;font-size:14px;line-height:14px;color:#FFFFFF;margin-bottom:14px}.footer .text-yellow{color:#ede852}

